
* {
  box-sizing: border-box;
}

body {
 
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;

}
.country-list{
  margin-left: 0px;
  padding-left: 0px;

  list-style: none;
}
.country-item{
  display: flex;
  align-items: center;
}
.country-flag {
 
  width: 30px;
  height: 30px;

  margin-right: 10px;

  border-radius: 50%;

}
.country-name {
  font-size: 14px;
}
.country-title {
  font-size: 24px;
  font-weight: 700;
  margin-bottom: 5px;
}
.country-desc{
  font-size: 18px;
}